const INSERT_SUPPLY_ITEM_LOADING = 'SELECT_GOODS/INSERT_SUPPLY_ITEM/INSERT_SUPPLY_ITEM_LOADING';
const INSERT_SUPPLY_ITEM_FAIL = 'SELECT_GOODS/INSERT_SUPPLY_ITEM/INSERT_SUPPLY_ITEM_FAIL';
const INSERT_SUPPLY_ITEM_SUCCESS = 'SELECT_GOODS/INSERT_SUPPLY_ITEM/INSERT_SUPPLY_ITEM_SUCCESS';

export default function (state = {loading:false}, action = {}) {
  switch (action.type) {
    /*    case INSERT_SUPPLY_ITEM_LOADING:
     return {
     ...state,
     };*/
    case INSERT_SUPPLY_ITEM_SUCCESS:
      // debugger;
      return {
        ...state,
        loading: false,
        loaded: true,
        data: action.result
      };
    case INSERT_SUPPLY_ITEM_FAIL:
      // debugger;
      return {
        ...state,
        loading: false,
        loaded: false,
        error: action.msg
      };
    default:
      return state
  }
}

export function insertShopSupplyItemInfo (values) {
  // debugger;
  return {
    types: [INSERT_SUPPLY_ITEM_LOADING, INSERT_SUPPLY_ITEM_SUCCESS, INSERT_SUPPLY_ITEM_FAIL],
    promise: (client) => client.post('/item/seller/supplier/insertShopSupplyItemInfo',{data:values})
    /*promise: (client) => client.get('/supplyTestData.json',{params:values})*/
  }
}